
* {
 
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}
body{
  /*background-image: url("../src/img/1\ \(2\).jpg");*/
  background-repeat: repeat;
  background-size:  100%;
  position:sticky;
  top: 0;
  z-index: 1;
  padding: 5px;

}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.products-main{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  position: relative;

}


.products-container{
  width: 90%;
  height: auto;
  min-height: 100vh;
  padding: 50px 0;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;
}
.single-product{
  width: 300px;
  height: 400px;
  overflow: hidden;
  background-color:rgb(230, 182, 123);
  margin: 5px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 5cm red 5cm;


}
.single-product img{
  
  width: 255px;
  height: 200px;
  object-fit: cover;
  margin-top: 20px;

}
.single-product img:hover{
  width:300px;
  border-radius: 50%;
}



.cart-main{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


}
.cart-container{

  width: 90%;
  height: auto;
  min-height: 100vh;
  padding: 50px 0;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;

}

  
  button{
    position: relative;
    background-color:rgb(212, 196, 155);
    border-radius: 5px;
    border: none;
    margin: 5px;
    font-weight: bold;
    font-size:medium;
    height: 30px;
    
}

.clicked::before{
  content: 'Add';
  height:100%;
  width: 100%;
  position: absolute;
  top:0;
  left: 0;
  background-color:darkorange;
  display: flex;
  justify-content: center;
  align-items: center;

}
input{
  padding: 5px;
}


.nav{
  background-color:antiquewhite;
  display: flex;
  justify-content:space-around;
  align-items: center;
  width: 100%;
  z-index: 3;
  position: sticky;
  top: 0;
  margin: 0px;
}

.nav-link{

  margin:0px;
  text-align: right;
  display: flex;
  justify-content:space-between;
  text-align: center;

  width: 50%;
  
  

}
.icon{

  display: flex;
  text-align: left;
  width: 30%;
  color:blue;
}

.NavLink,a{

  text-decoration: none;
  font-size:x-large;

}
icon,img{
  width: 100px;
  height: 80px;

}


.container{
  width: 85vw;
  height: 90vh;
  margin: auto;
}

.main{
  height:80%
}
.main img{
  height:100%;
  width:100%
}

.slider{
  height:20%;
  display:flex;
  justify-content:space-around;
}
.slider img{
  height:100%;
  width:18%
}
.img{
  width: 100px;
  height: 80px;
}
.imgsingle{
    height: 400px;
    width: 1000px;

}
.count{
  color: darkred;
  background-color: chocolate;
  
}
